body {
  background-color: aqua; }
  body .form {
    position: fixed;
    z-index: 1;
    margin: 5px; }

.container {
  width: 560px;
  background-color: white;
  margin: 0 auto; }
  .container header, .container section {
    position: fixed;
    text-align: center;
    width: 560px;
    background-color: white; }
    .container header .logo, .container section .logo {
      width: 120px;
      padding: 10px; }
  .container #video {
    top: 50px; }
  .container main {
    padding: 340px 18px 10px; }
    .container main article {
      display: flex;
      align-items: center;
      padding: 8px 12px;
      border: 2px solid white;
      margin: 0 auto; }
      .container main article:hover {
        border: 2px solid orange;
        border-radius: 10px; }
      .container main article .thumb {
        height: 100px; }
      .container main article .details {
        padding: 8px 22px;
        overflow: hidden; }
        .container main article .details h4 {
          padding: 0;
          margin: 0;
          line-height: 1.3;
          font-weight: 600; }
        .container main article .details p {
          padding: 0;
          margin: 0;
          line-height: 1.3;
          font-weight: 600;
          color: grey;
          font-size: 0.7rem; }
